import React, { useState } from 'react'

import styles from './table.module.css'
import chev from '../../assets/icons/Chevron-Right.png'
import filter from '../../assets/icons/Filter.png'
import arrowUpIcon from '../../assets/icons/Arrow-Up.png'
import triangleIcon from '../../assets/icons/Triangle-Down.png'

function TableComponent(props) {
  const [selectedItem, setSelectedItem] = useState({
    workerId: null,
    client: '',
    title: '',
    worker: '',
    started: '',
    ended: '',
    duration: '',
    status: '',
  })

  const incrementPageButtonNumbers = () => {
    props.incrementPageButtonNumbers()
  }

  const handlePreviousPage = () => {
    if (props.currentPage > 1) {
      props.getData(props.currentPage - 1, props.pageSize)
    }
  }

  const setCurrentPage = (page) => {
    props.getData(page, props.pageSize)
  }

  const handleNextPage = () => {
    if (props.currentPage < props.totalData / props.pageSize) {
      props.getData(props.currentPage + 1, props.pageSize)
    }
    incrementPageButtonNumbers()
  }

  const selectItem = (id) => {
    setSelectedItem(props.data[id])
  }

  const handlePageSize = (e) => {
    props.getData(1, e.target.value)
  }

  return (
    <>
      <div className={styles.vaiTableDiv}>
        <table>
          <thead>
            <tr>
              <td className={styles.vaiThClient}>
                CLIENT <img src={filter} alt="FilterIcon1" className="mx-2" />
              </td>
              <td className={styles.vaiThTitle}>
                TITLE <img src={filter} alt="FilterIcon2" className="mx-2" />
              </td>
              <td className={styles.vaiThWorker}>
                WORKER <img src={filter} alt="FilterIcon3" className="mx-2" />
              </td>
              <td className={styles.vaiThStarted}>
                STARTED <img src={filter} alt="FilterIcon4" className="mx-2" />
                <img src={arrowUpIcon} alt="ArrowUpIcon" />
              </td>
              <td className={styles.vaiThEnded}>
                ENDED <img src={filter} alt="FilterIcon5" className="mx-1" />
              </td>
              <td className={styles.vaiThDuration}>
                DURATION <img src={filter} alt="FilterIcon6" className="mx-2" />
              </td>
              <td className={styles.vaiThStatus}>
                STATUS <img src={filter} alt="FilterIcon" className="mx-2" />
              </td>
            </tr>
          </thead>
          <tbody
            className={`${styles.vaiTbody} ${styles.vaiTbodyDiv}`}
            style={{ overflowY: props.data.length > 12 ? 'auto' : null }}
          >
            {props.data.map((item, index) => {
              const successClassName =
                item.status === 'Success' ? styles.vaiSuccessStatus : null
              const runningClassName =
                item.status === 'Running' ? styles.vaiRunningStatus : null
              const failedClassName =
                item.status === 'Failed' ? styles.vaiFailedStatus : null
              const stoppedClassName =
                item.status === 'Stopped' ? styles.vaiStoppedStatus : null
              const queuedClassName =
                item.status === 'Queued' ? styles.vaiQueuedStatus : null
              return (
                <tr
                  className={
                    selectedItem.workerId === item.workerId
                      ? styles.vaiActiveRow
                      : null
                  }
                  key={index}
                  onClick={() => selectItem(index)}
                  style={{ cursor: 'pointer' }}
                >
                  <td className={styles.vaiTdClient}>{item.client}</td>
                  <td className={styles.vaiTdTitle}>
                    <div
                      style={{
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        width: '100%',
                      }}
                    >
                      {item.title}
                    </div>
                  </td>
                  <td className={styles.vaiTdWorker}>{item.worker}</td>
                  <td className={styles.vaiTdStarted}>{item.started}</td>
                  <td className={styles.vaiTdEnded}>{item.ended}</td>
                  <td className={styles.vaiTdDuration}>{item.duration}</td>
                  <td className={styles.vaiTdStatus}>
                    <div
                      className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
                    >
                      {item.status}
                    </div>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
      <div className={styles.vaiTableFooter}>
        <div className="row">
          <div className="col-12">
            <label className={styles.vaiLabel}>Listing</label>
            <select
              name="pageSize"
              id="tablePageSize"
              className={styles.vaiListSelection}
              onChange={handlePageSize}
            >
              <option
                className={styles.vaiOptionSelected}
                value={props.pageSize}
                selected
              >
                {props.pageSize}
              </option>
              <option
                value="12"
                selected={props.pageSize === 12 ? 'selected' : null}
              >
                12
              </option>
              <option
                value="15"
                selected={props.pageSize === 15 ? 'selected' : null}
              >
                15
              </option>
              <option
                value="50"
                selected={props.pageSize === 50 ? 'selected' : null}
              >
                50
              </option>
              <option
                value="100"
                selected={props.pageSize === 100 ? 'selected' : null}
              >
                100
              </option>
            </select>
            <label className={styles.vaiLabel}>
              entries, showing 1 to {props.pageSize} of {props.totalData}
            </label>

            <div
              className="btn-group float-end"
              role="group"
              aria-label="Basic example"
            >
              <button
                type="button"
                className={styles.vaiPaginationBtn}
                onClick={handlePreviousPage}
              >
                <img
                  src={chev}
                  alt="Paginate-Left"
                  className={styles.vaiChevLeftIcon}
                />
              </button>
              {props.pageButtons.map((button, index) => {
                return (
                  <button
                    key={index}
                    type="button"
                    onClick={() => setCurrentPage(button.buttonId)}
                    className={`${styles.vaiPaginationBtn} ${
                      button.buttonText === props.currentPage
                        ? styles.vaiPaginationActiveBtn
                        : null
                    }`}
                  >
                    {button.buttonText}
                  </button>
                )
              })}
              <button
                type="button"
                className={styles.vaiPaginationBtn}
                onClick={handleNextPage}
              >
                <img
                  src={chev}
                  alt="Paginate-Right"
                  className={styles.vaiChevRightIcon}
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default TableComponent
