import React, { useState, useEffect } from 'react'
import HeaderComponent from '../../shared/header/header.component'
import TableComponent from '../../shared/table/table.component'
import monitoringService from './monitoring.service'

function MonitoringComponent() {
  const [data, setData] = useState([])
  const [totalItems, setTotalItems] = useState(0)
  const [currentPage, setCurrentPage] = useState(0)
  const [pageSize, setPageSize] = useState(0)

  const [pageButtons, setPageButtons] = useState([
    {
      buttonId: 1,
      buttonText: 1,
    },
    {
      buttonId: 2,
      buttonText: 2,
    },
    {
      buttonId: 3,
      buttonText: 3,
    },
    {
      buttonId: 4,
      buttonText: 4,
    },
    {
      buttonId: 5,
      buttonText: 5,
    },
  ])

  const incrementPageButtonNumbers = () => {
    if (currentPage > 2) {
      setPageButtons([
        { buttonId: 1, buttonText: pageButtons[0].buttonText + 1 },
        { buttonId: 2, buttonText: pageButtons[1].buttonText + 1 },
        { buttonId: 3, buttonText: pageButtons[2].buttonText + 1 },
        { buttonId: 4, buttonText: pageButtons[3].buttonText + 1 },
        { buttonId: 5, buttonText: pageButtons[4].buttonText + 1 },
      ])
    }
  }

  const getAllMonitorings = (pageNumber = 1, pageSize = 12) => {
    setData([])
    setTotalItems(0)
    setPageSize(0)
    setCurrentPage(0)
    monitoringService
      .getAllMonitorings(pageNumber, pageSize)
      .then((res) => {
        setData(res.data.workerResultItems)
        setTotalItems(res.data.totalItemCount)
        setCurrentPage(pageNumber)
        setPageSize(pageSize)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  useEffect(() => {
    getAllMonitorings()
    return () => {}
  }, [])

  return (
    <>
      <HeaderComponent />
      <hr style={{ border: '1px inset #B1B1B1' }} />
      {data.length > 0 && totalItems > 0 && currentPage > 0 && pageSize > 0 ? (
        <TableComponent
          getData={getAllMonitorings}
          incrementPageButtonNumbers={incrementPageButtonNumbers}
          data={data}
          totalData={totalItems}
          currentPage={currentPage}
          pageSize={pageSize}
          pageButtons={pageButtons}
        />
      ) : null}
    </>
  )
}

export default MonitoringComponent
