import { useState } from 'react'
import { Route } from 'react-router'
import { useLocation, Redirect } from 'react-router-dom'
import ViewsComponent from './views/views.component'
import './App.css'
import './AppFont.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import LoginComponent from './components/login/login.component'
import NotFoundComponent from './components/not-found/notFound.component'
import authService from './core/auth.service'

function App() {
  const location = useLocation()
  const [isAuthenticated, setAuthentication] = useState(
    authService.isAuthenticated
  )

  const loggedIn = () => {
    setAuthentication(authService.isAuthenticated)
  }

  const loggedOut = () => {
    setAuthentication(authService.isAuthenticated)
  }

  return (
    <div className="App">
      {isAuthenticated ? (
        <>
          <div
            style={{
              display: location.pathname === '/not-found' ? 'none' : null,
            }}
          >
            <ViewsComponent loggedOut={loggedOut} />
          </div>
          <Route path="/not-found" component={NotFoundComponent} exact />
          <Route
            exact
            path="/login"
            render={() => {
              return <Redirect to="/monitoring" />
            }}
          />
        </>
      ) : (
        <>
          <Route
            path="/login"
            render={(props) => <LoginComponent loggedIn={loggedIn} />}
            exact
          />
          <Route
            exact
            path="*"
            render={() => {
              return <Redirect to="/login" />
            }}
          />
        </>
      )}
    </div>
  )
}

export default App
