import React, { useState } from 'react'
import styles from '../login.module.css'
import VirtualaiLogo from '../../../assets/icons/VirtualAiLogo.png'
import signInIcon from '../../../assets/icons/Sign-In.png'
import eyeIcon from '../../../assets/icons/Eye.png'
import { useHistory } from 'react-router-dom'
import authService from '../../../core/auth.service'

function SignInComponent(props) {
  const history = useHistory()
  const [showPassword, setShowPassword] = useState('password')
  const [visibleErrDialog, setVisibleErrDialog] = useState(false)
  const [email, setEmail] = useState()
  const [password, setPassword] = useState()

  const handleLogin = () => {
    const userInfo = {
      email: email,
      password: password,
    }
    authService
      .login(userInfo)
      .then((res) => {
        authService.setUserIdentity(res.data)
        props.loggedIn()
        history.push(`/monitoring`)
      })
      .catch((err) => {
        setVisibleErrDialog(true)
      })
  }

  const showForgotPasswordComponent = () => {
    props.showForgotPasswordComponent()
  }

  const togglePassword = () => {
    if (showPassword === 'password') {
      setShowPassword('text')
    } else {
      setShowPassword('password')
    }
  }

  return (
    <>
      <img
        src={VirtualaiLogo}
        alt="VirtualAiLogo"
        className={styles.vaiLoginLogo}
      />
      <p className={styles.vaiTitleText}>Sign In</p>
      <input
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        type="text"
        className={styles.vaiLoginEmailInput}
        placeholder="Email"
      />
      <br />
      <div className="input-group position-relative">
        <input
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          type={showPassword}
          className={styles.vaiLoginPasswordInput}
          placeholder="Pasword"
          aria-label="Pasword"
          aria-describedby="password-addon"
        />
        <div
          className={`${styles.vaiLoginPasswordInputIcon} input-group-append`}
        >
          <img
            src={eyeIcon}
            alt="EyeIcon"
            style={{ cursor: 'pointer' }}
            onClick={togglePassword}
          />
        </div>
      </div>
      <p
        className={styles.vaiErrorAlertText}
        style={{ display: visibleErrDialog ? null : 'none' }}
      >
        Invalid email or password, please try again
      </p>
      <button
        className={styles.vaiSignInButton}
        onClick={handleLogin}
        style={{ marginTop: visibleErrDialog ? '0px' : null }}
      >
        Sign In{' '}
        <img
          src={signInIcon}
          alt="SignInIcon"
          className={styles.vaiSignInButtonIcon}
        />
      </button>
      <p
        className={styles.vaiForgotPasswordText}
        onClick={showForgotPasswordComponent}
        style={{ cursor: 'pointer' }}
      >
        Forgot password?
      </p>
    </>
  )
}

export default SignInComponent
